$(document).ready(function () {
    var isAdOpen = false;
    console.log(1)
    $("#ad-button").click(function () {
        if (!isAdOpen) {
            $("#ad-button-text").text("關閉視窗");
            $('#ad-image-open').show();
            $("#ad-open-icon").addClass("fa-times").removeClass("fa-chevron-down");
        } else {
            $("#ad-button-text").text("展開看看");
            $('#ad-image-open').hide();  
            $("#ad-open-icon").addClass("fa-chevron-down").removeClass("fa-times");
        }
        isAdOpen = !isAdOpen;
    });
    console.log(2)
    $("#m-banner-close-button").click(function(){
        console.log(3)
        $("#m-banner").hide();
    });
});